.footer {
  text-align: center;
  margin: 20px 0 0 0;
  /* padding-bottom: 20px; */
}
.footer__btn {
  cursor: pointer;
  margin-top: 5px;
  width: 100px;
  height: 28px;
  border: none;
  border-radius: 5px;
  background-color: rgba(86, 63, 214, 0.555);
  font-size: 16px;
  color: #fff;
}
.footer__btn:active,
.footer__btn:focus {
  outline: none;
}
.footer__btn:active {
  background-color: rgba(86, 63, 214, 0);
  color: #000000;
  font-size: 16px;
  font-weight: 600;
  border: 1px solid #000;
}
